import * as PIXI from "pixi.js";
import * as Tween from "gsap";

export class FramLight extends PIXI.Container {

    constructor() {
        super();
        this._lights = [];
    }

    init(texture) {

        for (let i = 0; i < 5; i++) {
            let templight = PIXI.Sprite.from(texture);
            this.addChild(templight);
            templight.blendMode = PIXI.BLEND_MODES.ADD;
            templight.anchor.set(0.5);
            templight.position.set(160 + i * 200, 525);
            templight.alpha = 1;
            templight.visible = false;
            this._lights.push(templight);
        }
    }

    reset() {
        for (let i = 0; i < 5; i++) {
            this._lights[i].visible = false;
            this._lights[i].alpha = 1;
        }
    }

    play(index) {
        this._lights[index].visible = true;

        Tween.TweenMax.to(this._lights[index], 0.3,
            {
                alpha: 0,
                ease: Tween.Sine.easeInOut,
            })

    }
}