import * as PIXI from "pixi.js";

export class NextIssueInfo extends PIXI.Container {

    init() {
        this._info_te = new PIXI.extras.BitmapText(" 00:00:00\n 00:00:00", { font: "80px cumstom2" });
        this.addChild(this._info_te);
        this._info_te.anchor.set(0.5);
        this._info_te.position.set((this.parent.StageWidth - this._info_te.width / 2) - 5, 5);
    }

    updata(data) {
        this._info_te.text = "" + data["nextTime"] + "\n" + data["currTime"];
    }
}