import * as PIXI from "pixi.js";
import { Tool } from "../../../Tools/Tool";

export class Firework extends PIXI.Container {

    constructor() {
        super();

        this._colors = [0x38FEEE, 0x75FE38, 0xEEFE38, 0xD25138, 0xD238FE];
    }


    init(data) {
        this._fire01 = PIXI.extras.AnimatedSprite.fromFrames(data);
        this._fire01.filters = [this.colorMatrix(this._colors[3], PIXI.BLEND_MODES.ADD)];
        this.addChild(this._fire01);
        this._fire01.animationSpeed = 0.3;
        this._fire01.position.set(276, 156);
        this._fire01.loop = false;
        this._fire01.visible = false;

        this._fire02 = PIXI.extras.AnimatedSprite.fromFrames(data);
        this._fire02.filters = [this.colorMatrix(this._colors[1], PIXI.BLEND_MODES.ADD)];
        this.addChild(this._fire02);
        this._fire02.animationSpeed = 0.3;
        this._fire02.position.set(45, 331);
        this._fire02.loop = false;
        this._fire02.visible = false;

        this._fire03 = PIXI.extras.AnimatedSprite.fromFrames(data);
        this._fire03.filters = [this.colorMatrix(this._colors[3], PIXI.BLEND_MODES.ADD)];
        this.addChild(this._fire03);
        this._fire03.animationSpeed = 0.3;
        this._fire03.position.set(765, 148);
        this._fire03.loop = false;
        this._fire03.visible = false;

        this._fire04 = PIXI.extras.AnimatedSprite.fromFrames(data);
        this._fire04.filters = [this.colorMatrix(this._colors[0], PIXI.BLEND_MODES.ADD)];
        this.addChild(this._fire04);
        this._fire04.animationSpeed = 0.3;
        this._fire04.position.set(1024, 299);
        this._fire04.loop = false;
        this._fire04.visible = false;

        this._fire05 = PIXI.extras.AnimatedSprite.fromFrames(data);
        this._fire05.filters = [this.colorMatrix(this._colors[4], PIXI.BLEND_MODES.ADD)];
        this.addChild(this._fire05);
        this._fire05.animationSpeed = 0.3;
        this._fire05.position.set(907, 44);
        this._fire05.loop = false;
        this._fire05.visible = false;

        this._fire06 = PIXI.extras.AnimatedSprite.fromFrames(data);
        this._fire06.filters = [this.colorMatrix(this._colors[2], PIXI.BLEND_MODES.ADD)];
        this.addChild(this._fire06);
        this._fire06.animationSpeed = 0.3;
        this._fire06.position.set(164, 47);
        this._fire06.loop = false;
        this._fire06.visible = false;

        this.pivot.x = this.parent.StageWidth * 0.5;
        this.pivot.y = this.parent.StageHeight * 0.5;
        this.x = this.parent.StageWidth * 0.4;
        this.y = this.parent.StageHeight * 0.3;
    }

    reset() {
        this._fire01.visible = false;
        this._fire02.visible = false;
        this._fire03.visible = false;
        this._fire04.visible = false;
        this._fire05.visible = false;
        this._fire06.visible = false;
    }

    play() {
        setTimeout(() => { this._fire01.visible = true; this._fire01.gotoAndPlay(0); }, 300);
        setTimeout(() => { this._fire02.visible = true; this._fire02.gotoAndPlay(0); }, 600);
        setTimeout(() => { this._fire03.visible = true; this._fire03.gotoAndPlay(0); }, 900);
        setTimeout(() => { this._fire04.visible = true; this._fire04.gotoAndPlay(0); }, 1200);
        setTimeout(() => { this._fire05.visible = true; this._fire05.gotoAndPlay(0); }, 1500);
        setTimeout(() => { this._fire06.visible = true; this._fire06.gotoAndPlay(0); }, 1800);
    }


    colorMatrix(hex, blendMode) {
        let color = this.hexToRgb(hex);

        let result = new PIXI.filters.ColorMatrixFilter();
        result.blendMode = blendMode;
        result.matrix = [
            color.r / 255, 0, 0, 0, 0,
            0, color.g / 255, 0, 0, 0,
            0, 0, color.b / 255, 0, 0,
            0, 0, 0, 1, 0
        ];
        return result;
    }

    hexToRgb(hex) {
        let tmephex = hex;
        let result;
        if (Number.isInteger(tmephex)) {
            tmephex = "0x" + Tool.PadLeft(tmephex.toString(16), 6, "0").toUpperCase();
            result = /^0x?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec("" + tmephex);
        }
        else {
            result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec("" + tmephex);
        }


        return result ? {
            r: parseInt(result[1], 16),
            g: parseInt(result[2], 16),
            b: parseInt(result[3], 16)
        } : null;
    }
}