import * as PIXI from "pixi.js";

export class SlotObj {

    constructor(id, texture = null, pos = null) {
        this._id = id;
        this._texture = texture;
        this._pos = pos == null ? (new PIXI.Point(0, 0)) : pos;
    }

    get Id() {
        return this._id;
    }

    get Pos() {
        return this._pos;
    }

    set Pos(v) {
        this._pos = v;
    }

    get Texture() {
        return this._texture;
    }

    set Texture(v) {
        this._texture = v;
    }
}