import * as PIXI from "pixi.js";
import * as Tween from "gsap";

export class Light extends PIXI.Container {

    constructor() {
        super();

        this._light01_1;
        this._light01_2;
        this._light02_1;
        this._light02_2;
        this._light03_1;
        this._light03_2;
        this._light04_1;
        this._light04_2;

        this._lights = [];
        this._lightTweens = [];

        this._tweenAlpah = 1;
    }

    init(data) {

        this._light01_1 = PIXI.Sprite.from(data.light01);
        this.addChild(this._light01_1);
        this._light01_1.blendMode = PIXI.BLEND_MODES.ADD;
        this._light01_1.scale.set(0.8);
        this._light01_1.anchor.set(0.5, 1);
        this._light01_1.rotation = 0.3;
        this._light01_1.x = 154.49;
        this._light01_1.y = 465.69;
        this._light01_1.visible = false;

        this._light01_2 = PIXI.Sprite.from(data.light01);
        this.addChild(this._light01_2);
        this._light01_2.blendMode = PIXI.BLEND_MODES.ADD;
        this._light01_2.scale.set(0.8);
        this._light01_2.anchor.set(0.5, 1);
        this._light01_2.rotation = -0.3;
        this._light01_2.x = 959.54;
        this._light01_2.y = 465.69;
        this._light01_2.visible = false;

        this._light02_1 = PIXI.Sprite.from(data.light02);
        this.addChild(this._light02_1);
        this._light02_1.blendMode = PIXI.BLEND_MODES.ADD;
        this._light02_1.scale.set(0.8);
        this._light02_1.anchor.set(0.5, 1);
        this._light02_1.rotation = 0.48;
        this._light02_1.x = 0;
        this._light02_1.y = 575.59;
        this._light02_1.visible = false;

        this._light02_2 = PIXI.Sprite.from(data.light02);
        this.addChild(this._light02_2);
        this._light02_2.blendMode = PIXI.BLEND_MODES.ADD;
        this._light02_2.scale.set(0.8);
        this._light02_2.anchor.set(0.5, 1);
        this._light02_2.rotation = -0.48;
        this._light02_2.x = 1100;
        this._light02_2.y = 575.59;
        this._light02_2.visible = false;

        this._light03_1 = PIXI.Sprite.from(data.light03);
        this.addChild(this._light03_1);
        this._light03_1.blendMode = PIXI.BLEND_MODES.ADD;
        this._light03_1.scale.set(0.8);
        this._light03_1.anchor.set(0.5, 1);
        this._light03_1.rotation = -0.15;
        this._light03_1.x = 144.78;
        this._light03_1.y = 464;
        this._light03_1.visible = false;

        this._light03_2 = PIXI.Sprite.from(data.light03);
        this.addChild(this._light03_2);
        this._light03_2.blendMode = PIXI.BLEND_MODES.ADD;
        this._light03_2.scale.set(0.8);
        this._light03_2.anchor.set(0.5, 1);
        this._light03_2.rotation = 0.15;
        this._light03_2.x = 971.66;
        this._light03_2.y = 464;
        this._light03_2.visible = false;

        this._light04_1 = PIXI.Sprite.from(data.light04);
        this.addChild(this._light04_1);
        this._light04_1.blendMode = PIXI.BLEND_MODES.ADD;
        this._light04_1.scale.set(0.8);
        this._light04_1.anchor.set(0.5, 1);
        this._light04_1.rotation = -0.48;
        this._light04_1.x = 274.78;
        this._light04_1.y = 380;
        this._light04_1.visible = false;

        this._light04_2 = PIXI.Sprite.from(data.light04);
        this.addChild(this._light04_2);
        this._light04_2.blendMode = PIXI.BLEND_MODES.ADD;
        this._light04_2.scale.set(0.8);
        this._light04_2.anchor.set(0.5, 1);
        this._light04_2.rotation = 0.48;
        this._light04_2.x = 812.16;
        this._light04_2.y = 380;
        this._light04_2.visible = false;

        this._lights.push(this._light01_1, this._light01_2, this._light02_1, this._light02_2, this._light03_1, this._light03_2, this._light04_1, this._light04_2);

        this._lightTweens.push(
            Tween.TweenMax.to(this._light01_1, 1,
                {
                    paused: true,
                    rotation: -0.3,
                    repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    yoyo: true,
                    yoyoEase: Tween.Sine.easeInOut,
                }),
            Tween.TweenMax.to(this._light01_2, 1,
                {
                    paused: true,
                    rotation: 0.3,
                    repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    yoyo: true,
                    yoyoEase: Tween.Sine.easeInOut,
                }),
            Tween.TweenMax.to(this._light02_1, 1,
                {
                    paused: true,
                    rotation: -0.28,
                    repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    yoyo: true,
                    yoyoEase: Tween.Sine.easeInOut,
                }),
            Tween.TweenMax.to(this._light02_2, 1,
                {
                    paused: true,
                    rotation: 0.28,
                    repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    yoyo: true,
                    yoyoEase: Tween.Sine.easeInOut,
                }),
            Tween.TweenMax.to(this._light03_1, 1,
                {
                    paused: true,
                    rotation: 0.3,
                    repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    yoyo: true,
                    yoyoEase: Tween.Sine.easeInOut,
                }),
            Tween.TweenMax.to(this._light03_2, 1,
                {
                    paused: true,
                    rotation: -0.3,
                    repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    yoyo: true,
                    yoyoEase: Tween.Sine.easeInOut,
                }),
            Tween.TweenMax.to(this._light04_1, 1,
                {
                    paused: true,
                    rotation: 0.2,
                    repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    yoyo: true,
                    yoyoEase: Tween.Sine.easeInOut,
                }),
            Tween.TweenMax.to(this._light04_2, 1,
                {
                    paused: true,
                    rotation: -0.2,
                    repeat: -1,
                    ease: Tween.Sine.easeInOut,
                    yoyo: true,
                    yoyoEase: Tween.Sine.easeInOut,
                }));
    }

    reset() {
        for (let i = 0; i < 8; i++) {
            this._lights[i].alpha = 1;
            this._lights[i].visible = false;
            this._lightTweens[i].paused(true);
        }

        this._tweenAlpah = 1;
    }

    play() {
        for (let i = 0; i < 8; i++) {
            this._lights[i].visible = true;
            this._lightTweens[i].paused(false);
        }
    }

    stop() {
        Tween.TweenMax.to(this, 1,
            {
                ease: Tween.Sine.easeIn,
                _tweenAlpah: 0,
                onUpdate: () => {
                    for (let i = 0; i < this._lights.length; i++) {
                        this._lights[i].alpha = this._tweenAlpah;
                    }
                },
                onComplete: () => {
                    this.reset();
                }
            });
    }
}