import * as PIXI from "pixi.js";
import * as Tween from "gsap/TweenMax";

export class Logo extends PIXI.Container {

    constructor() {
        super();
        this._logo;
        this._blendLogo;
        this._logoTween;

        this._blendTextures = [];
        this._currTime = 0;
    }


    init(data) {
        this._logo = new PIXI.Sprite(data["base"]);
        this.addChild(this._logo);

        this._blendTextures = data["blend"];

        this._blendLogo = new PIXI.Sprite(this._blendTextures[2]);
        this.addChild(this._blendLogo);
        this._blendLogo.visible = false;
        this._blendLogo.blendMode = PIXI.BLEND_MODES.ADD;

        this.pivot.x = this.width * 0.5;
        this.pivot.y = this.height * 0.5;
        this.x = this.parent.StageWidth * 0.5;
        this.y = this.parent.StageHeight * 0.5;

        this._logoTween = Tween.TweenMax.to(this, (0.15 * this._blendTextures.length),
            {
                paused: true,
                ease: Tween.Sine.easeInOut,
                repeat: -1,
                yoyo: true,
                yoyoEase: Tween.Sine.easeInOut,
                _currTime: this._blendTextures.length - 1,
                onUpdate: () => {
                    this._blendLogo.texture = this._blendTextures[Math.floor(this._currTime)];
                }

            });
    }

    reset() {
        this._blendLogo.visible = false;
        this._logoTween.paused(true);
    }

    play() {
        this._blendLogo.visible = true;
        this._logoTween.paused(false);
    }
}