import MVC from "puremvc";
import { GameProxyEvent } from "../../Event/GameEvent";

export class SlotMediator extends MVC.Mediator {

    static get NAME() {
        return "SlotMediator";
    }

    constructor(viewComponent) {
        super(SlotMediator.NAME, viewComponent);
    }

    listNotificationInterests() {
        return [
            GameProxyEvent.INITSLOTOBJ,
            GameProxyEvent.UPSLOTOBJ,
        ];
    }

    handleNotification(notification) {
        switch (notification.getName()) {
            case GameProxyEvent.INITSLOTOBJ:
                this.viewComponent.initSlot(notification.getBody());
                break;
            case GameProxyEvent.UPSLOTOBJ:
                this.viewComponent.upSlot(notification.getBody());
                break;
            default:
                break;
        }
    }
}

