import * as PIXI from "pixi.js";
import { Tool } from "../../../Tools/Tool";
import { GameState } from "../../Common/GameState";
import { Shap } from "./Shap";

export class GameInfo extends PIXI.Container {

    //1234567890:
    init() {

        this.shape = new Shap(0x000000, 0.6, 500, 100);
        this.addChild(this.shape);
        this.shape.position.set(-this.shape.width * 0.5, -this.shape.height * 0.5);
        this.shape.visible = true;

        this._gameInfo_te = new PIXI.extras.BitmapText(":00:00:00", { font: '80px cumstom1' });
        this.addChild(this._gameInfo_te);
        this._gameInfo_te.anchor.set(0.5);
    }

    upGameInfo(data) {
        if (data["state"] !== this._currGameState) {
            switch (data["state"]) {
                case GameState.stop:
                case GameState.init:
                    this.visible = false;
                    break;
                default:
                    this.visible = true;
                    break;
            }

            this._currGameState = data["state"];
        }


        switch (data["state"]) {
            case GameState.counttime:
                this._gameInfo_te.text = ":" + this.conventToTime(data["time"]);
                break;
            case GameState.sealplate:
                this._gameInfo_te.text = "";
                break;
            case GameState.opening:
                this._gameInfo_te.text = "";
                break;
            default:
                break;
        }
    }


    conventToTime(num) {
        let h = Tool.PadLeft(Math.floor((num / 60) / 60), 2, "0");
        let m = Tool.PadLeft(Math.floor((num / 60) % 60), 2, "0");
        let s = Tool.PadLeft(num % 60, 2, "0");
        return h + ":" + m + ":" + s
    }
}