import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { GameMediator } from "../Mediator/GameMediator";
import { Light } from "../Component/Light";
import { Firework } from "../Component/Firework";
import { Logo } from "../Component/Logo";
import { SlotGroup } from "../Component/SlotGroup";
import { GameInfo } from "../Component/GameInfoText";
import * as Tween from "gsap/TweenMax";
import { FramLight } from "../Component/FramLight";
import { PreNumber } from "../Component/PreNumber";
import { NextIssueInfo } from "../Component/NextIssueInfo";
import { SoundBtn } from "../Component/SoundBtn";
import { Issue } from "../Component/Issue";
import { Shap } from "../Component/Shap";

export class GamePanel extends PIXI.Container {
    constructor(data) {
        super();
        this._multitonKey = data.key;
        this._stagewidth = data.width;
        this._stageheight = data.height;

        this.once("added", this.onAddToStage);
    }

    onAddToStage() {

        this._background = new PIXI.Sprite();
        this.addChild(this._background);
        this._light = new Light();
        this.addChild(this._light);
        this._lightBackground = new PIXI.Sprite();
        this.addChild(this._lightBackground);

        this._bottomShape = new Shap(0x000000, 0.7, this._stagewidth, this._stageheight * 0.16);
        this.addChild(this._bottomShape);
        this._bottomShape.visible = true;
        this._topbackground = new PIXI.Sprite();
        this.addChild(this._topbackground);
        this._nextIssueInfoComponet = new NextIssueInfo();
        this.addChild(this._nextIssueInfoComponet);
        this._preNumber = new PreNumber();
        this.addChild(this._preNumber);

        this._firework = new Firework();
        this.addChild(this._firework);

        this._issue = new Issue();
        this.addChild(this._issue);

        this._logoComponent = new Logo();
        this.addChild(this._logoComponent);

        this._fame = new PIXI.Sprite();
        this.addChild(this._fame);

        this._slotGroup = new SlotGroup();
        this.addChild(this._slotGroup);



        this._gameInfoText = new GameInfo();
        this.addChild(this._gameInfoText);

        this._soundbtn = new SoundBtn();
        this.addChild(this._soundbtn);

        this._slotMask = new PIXI.Graphics();
        this._slotMask.beginFill(0xffffff, 0);
        this._slotMask.drawRect(0, 0, 1125, 145);
        this._slotMask.endFill();
        this.addChild(this._slotMask);
        this._slotMask.pivot.x = this._slotMask.width * 0.5;
        this._slotMask.pivot.y = this._slotMask.height * 0.5;
        this._slotMask.position.set(this.StageWidth * 0.5, this.StageHeight * 0.71);

        this._framLight = new FramLight();
        this.addChild(this._framLight);


        ApplicationFacade.getInstance(this._multitonKey).registerMediator(new GameMediator(this));
    }


    init(data) {
        this._background.texture = PIXI.Texture.from(data.bg);
        this._light.init(data.lightbar);
        this._lightBackground.texture = PIXI.Texture.from(data.lightbg);
        this._lightBackground.blendMode = PIXI.BLEND_MODES.ADD;
        this._lightBackground.alpha = 0;
        this._lightBackground.visible = false;
        this._lightBgTween = Tween.TweenMax.to(this._lightBackground, 0.25,
            {
                paused: true,
                alpha: 1,
                ease: Tween.Sine.easeInOut,
                repeat: -1,
                yoyo: true,
                yoyoEase: Tween.Sine.easeInOut,
            });
        this._firework.init(data.fireworks);
        this._bottomShape.position.y = this._stageheight * 0.84;
        this._topbackground.texture = PIXI.Texture.from(data.topbg);
        this._topbackground.position.y = this._stageheight * 0.85;
        this._issue.init();
        this._preNumber.init();
        this._preNumber.position.y = this._stageheight * 0.85;
        // this._logoComponent.init(data["logo"]);
        this._fame.texture = PIXI.Texture.from(data.fame);
        this._fame.anchor.set(0.5);
        this._fame.position.set(this.StageWidth * 0.5, this.StageHeight * 0.72);
        this._gameInfoText.init(data.gameInfo);
        this._gameInfoText.position.set(this._stagewidth * 0.5, this._stageheight * 0.5);
        this._slotGroup.mask = this._slotMask;
        this._framLight.init(data.famelight);
        this._soundbtn.init(data.sound);
        this._soundbtn.scale.set(0.9)
        this._soundbtn.position.set(this._stagewidth * 0.73, 2);
    }

    startSlot() {
        this._lightBackground.visible = true;
        this._lightBgTween.paused(false);
        this._light.play();
        // this._logoComponent.play();
    }

    slotComplete() {
        this._light.stop();
        // this._logoComponent.reset();
        this._lightBackground.visible = false;
        this._lightBgTween.paused(true);
        this._framLight.reset();
    }

    midComplete() {
        // this._flashMask.alpha = 1;
        // this._flashMask.visible = true;
        // this._flashTween2.play(0);

        // this._background01_light.visible = true;
        // this._light01BgTween.paused(false);

        // this._background02.visible = false;
        // this._background02_light.visible = false;
        // this._light02BgTween.paused(true);

        // this._logoComponent.reset();
        // this._lightComponent.stop();
        this._firework.play();
    }

    get MultitonKey() {
        return this._multitonKey;
    }

    get StageWidth() {
        return this._stagewidth;
    }

    get StageHeight() {
        return this._stageheight;
    }

    get Soundbtn() {
        return this._soundbtn;
    }

    get GameInfoText() {
        return this._gameInfoText;
    }

    get Issue() {
        return this._issue;
    }

    get PreNumber() {
        return this._preNumber;
    }

    get Firework() {
        return this._firework;
    }

    get FramLight() {
        return this._framLight;
    }

}