import * as PIXI from "pixi.js";
import { ApplicationFacade } from "../../App/ApplicationFacade";
import { SlotMediator } from "../Mediator/SlotMediator";
import { Map } from "../../../Tools/Map";

export class SlotGroup extends PIXI.Container {

    constructor() {
        super();

        this.once("added", this.onAddToStage);
    }

    onAddToStage() {
        this._slotobj_map = new Map();

        ApplicationFacade.getInstance(this.parent.MultitonKey).registerMediator(new SlotMediator(this));
    }

    initSlot(data) {
        let tempSlotObj;
        tempSlotObj = PIXI.Sprite.from(data["texture"]);
        tempSlotObj.visible = data.visible;
        this.addChild(tempSlotObj);
        tempSlotObj.position.set(data["pos"].x, data["pos"].y);
        this._slotobj_map.push(data["id"], tempSlotObj);
    }

    upSlot(data) {
        if (this._slotobj_map.containsKey(data["id"])) {
            let tempSlotObj = this._slotobj_map.getValue(data["id"]);
            tempSlotObj.visible = data.visible;
            tempSlotObj.texture = PIXI.Texture.from(data["texture"]);
            tempSlotObj.x = data["pos"].x;
            tempSlotObj.y = data["pos"].y;
        }
    }
}
