import { SlotObj } from "./SlotObj";
import { GameConfig } from "../Data/GameConfig";
import { Map } from "../../../Tools/Map";
import { GameProxyEvent } from "../../Event/GameEvent";
import { ticker } from "pixi.js";

export class VerticalSlotGroupObj {


    constructor(rowindex, gameProxy) {
        this._rowIndex = rowindex;
        this._columCount = GameConfig.ObjColumnCount;
        this._gameProxy = gameProxy;

        this._ticker;

        this._rowIndex;
        this._columCount;

        this._slotCount = 0;
        this._slotSpeed = 0;
        this._isSlot = false;
        this._isStop = false;
        this._isPrveVSC = false;
        this._isSimulation = false;

        this._obj_map;

        this._gameProxy;


        this._onSlotComplete = null;

        this._onSlotStopComplete = null;

        this.init();
    }

    init() {
        this._ticker = new ticker.Ticker();
        this._ticker.add(() => this.update());

        this._obj_map = new Map();

        for (let i = 0; i < this._columCount; i++) {
            let tempObj = new SlotObj(this._rowIndex + "_" + i, this._gameProxy.getRenderTexture());
            this._obj_map.push(tempObj.Id, tempObj);
            tempObj.Pos.x = GameConfig.ObjStart_X + this._rowIndex * GameConfig.Obj_Increment_X;
            tempObj.Pos.y = GameConfig.ObjStart_Y + i * GameConfig.Obj_Increment_Y;

            this._gameProxy.sendNotification(GameProxyEvent.INITSLOTOBJ, { "id": tempObj.Id, "pos": tempObj.Pos, "texture": tempObj.Texture, visible: false });
        }
    }

    stop() {
        this._ticker.stop();
    }

    reset() {
        for (let i = 0; i < this._obj_map.length; i++) {
            let tempObj = this._obj_map.element(i).Value;
            tempObj.Pos.y = GameConfig.ObjStart_Y + i * GameConfig.Obj_Increment_Y;
            this._gameProxy.sendNotification(GameProxyEvent.UPSLOTOBJ, { "id": tempObj.Id, "pos": tempObj.Pos, "texture": tempObj.Texture, visible: false });
        }
    }

    startSlot(isSimulation, delay) {
        this._slotCount = GameConfig.SlotCount;
        this._slotSpeed = GameConfig.SlotMinSpeed;
        this._isStop = false;
        this._isSlot = true;
        this._isPrveVSC = false;
        this._isSimulation = isSimulation;
        setTimeout(() => {
            this._ticker.start();
        }, delay);
    }

    stopSlot() {
        if (!this._isSlot) {
            setTimeout(() => {
                if (this._onSlotStopComplete !== null) {
                    this._onSlotStopComplete(this._rowIndex);
                }

                if (this._onSlotComplete !== null) {
                    this._onSlotComplete();
                }
                this.setNumber(this._gameProxy.CurrNum[this._rowIndex]);
            }, this._rowIndex * 500);
        }
        else this._isStop = true;
    }

    update() {
        if (!this._isStop) {
            this._slotSpeed += GameConfig.SlotIncrement * 0.2;
            this._slotSpeed = Math.min(this._slotSpeed, GameConfig.SlotMaxSpeed);
            this._slotCount--;

            if (this._slotCount === 0 && this._isSimulation) this._isStop = true;
            for (let i = 0; i < this._obj_map.length; i++) {
                let tempObj = this._obj_map.element(i).Value;
                tempObj.Pos.y += this._slotSpeed;
                if (tempObj.Pos.y >= GameConfig.ObjStart_Y + (this._obj_map.length - 1) * GameConfig.Obj_Increment_Y) {
                    tempObj.Texture = this._gameProxy.getRenderTexture();
                    tempObj.Pos.y -= GameConfig.Obj_Increment_Y * this._obj_map.length;
                }
                this._gameProxy.sendNotification(GameProxyEvent.UPSLOTOBJ,
                    {
                        visible: true,
                        "id": tempObj.Id,
                        "pos": tempObj.Pos,
                        "texture": tempObj.Texture
                    });
            }
        }
        else {
            this._slotSpeed -= GameConfig.SlotIncrement * 0.2;
            this._slotSpeed = Math.max(this._slotSpeed, GameConfig.SlotMinSpeed);

            if (this._slotSpeed !== GameConfig.SlotMinSpeed) {
                for (let i = 0; i < this._obj_map.length; i++) {
                    let tempObj = this._obj_map.element(i).Value;
                    tempObj.Pos.y += this._slotSpeed;

                    if (tempObj.Pos.y >= GameConfig.ObjStart_Y + (this._columCount - 1) * GameConfig.Obj_Increment_Y) {
                        if (i === 2) {
                            tempObj.Texture = this._gameProxy.getRenderTexture(this._gameProxy.CurrNum[this._rowIndex]);
                        }
                        else tempObj.Texture = this._gameProxy.getRenderTexture();

                        tempObj.Pos.y -= GameConfig.Obj_Increment_Y * this._columCount;
                    }
                    this._gameProxy.sendNotification(GameProxyEvent.UPSLOTOBJ,
                        {
                            visible: true,
                            "id": tempObj.Id,
                            "pos": tempObj.Pos,
                            "texture": tempObj.Texture
                        });
                }
            }
            else {
                this._slotSpeed = Math.min(this._slotSpeed,
                    (GameConfig.ObjStart_Y + (this._columCount - 1) * GameConfig.Obj_Increment_Y) - this._obj_map.element(this._obj_map.length - 1).Value.Pos.y);

                for (var i = 0; i < this._obj_map.length; i++) {
                    let tempObj = this._obj_map.element(i).Value;
                    tempObj.Pos.y += this._slotSpeed;

                    if (this._isPrveVSC) {
                        //console.log( ( tempObj.Pos.y < GameConfig.ObjStart_Y + 2 * GameConfig.Obj_Increment_Y ) );

                        if (tempObj.Pos.y <= GameConfig.ObjStart_Y + 2 * GameConfig.Obj_Increment_Y) {
                            tempObj.Texture = this._gameProxy.getRenderTexture(this._gameProxy.CurrNum[this._rowIndex]);
                        }

                        if (tempObj.Pos.y === GameConfig.ObjStart_Y + 2 * GameConfig.Obj_Increment_Y) {
                            this._ticker.stop();
                            this._isSlot = false;

                            if (this._onSlotStopComplete !== null) {
                                this._onSlotStopComplete(this._rowIndex);
                            }

                            if (this._onSlotComplete !== null) {
                                this._onSlotComplete();
                            }
                        }
                        if (tempObj.Pos.y >= GameConfig.ObjStart_Y + (this._columCount - 1) * GameConfig.Obj_Increment_Y) {
                            tempObj.Pos.y -= GameConfig.Obj_Increment_Y * this._columCount;
                            tempObj.Texture = this._gameProxy.getRenderTexture(this._gameProxy.CurrNum[this._rowIndex]);
                        }

                        /*if ( i === 2 )
                        {
                            tempObj.Texture = this._gameProxy.getRenderTexture( this._gameProxy.CurrNum[ this._rowIndex ] );
                        }

                        if ( i === this._obj_map.length - 1 && tempObj.Pos.y === GameConfig.ObjStart_Y + ( this._columCount - 1 ) * GameConfig.Obj_Increment_Y )
                        {
                            this._ticker.stop();
                            this._isSlot = false;

                            if ( this._onSlotStopComplete !== null )
                            {
                                this._onSlotStopComplete( this._rowIndex );
                            }

                            if ( this._onSlotComplete !== null )
                            {
                                this._onSlotComplete();
                            }
                        }
                        else if ( tempObj.Pos.y >= GameConfig.ObjStart_Y + ( this._columCount - 1 ) * GameConfig.Obj_Increment_Y )
                        {
                            tempObj.Pos.y -= GameConfig.Obj_Increment_Y * this._columCount;
                        }*/

                    }
                    else if (tempObj.Pos.y >= GameConfig.ObjStart_Y + (this._columCount - 1) * GameConfig.Obj_Increment_Y) {
                        tempObj.Texture = this._gameProxy.getRenderTexture();

                        tempObj.Pos.y -= GameConfig.Obj_Increment_Y * this._columCount;
                    }


                    this._gameProxy.sendNotification(GameProxyEvent.UPSLOTOBJ,
                        {
                            visible: true,
                            "id": tempObj.Id,
                            "pos": tempObj.Pos,
                            "texture": tempObj.Texture
                        });
                }
            }
        }
    }

    get IsSlot() {
        return this._isSlot;
    }

    set IsPrveVSC(v) {
        this._isPrveVSC = v;
    }

    setNumber(num) {
        let tempObj = this._obj_map.element(2).Value;
        tempObj.Texture = this._gameProxy.getRenderTexture(num);
        this._gameProxy.sendNotification(GameProxyEvent.UPSLOTOBJ,
            {
                visible: true,
                "id": tempObj.Id,
                "pos": tempObj.Pos,
                "texture": tempObj.Texture
            });
    }

    set onSlotComplete(v) {
        this._onSlotComplete = v;
    }

    set onSlotStopComplete(v) {
        this._onSlotStopComplete = v;
    }
}